<template>
   <div class="media m-2 rounded material-depth-1">
      <img class="mr-3 rounded"
           :src="imgsrc"
           alt="Generic placeholder image">
      <div class="media-body">
         <h5 class="mt-0">{{title}}</h5>
         {{text}}
      </div>
   </div>
</template>

<script>
export default {
  name: "Media",
  props: {
    text: String,
    title: String,
    imgsrc: String
  }
};
</script>

<style scoped>
.media {
  transition: box-shadow 0.7s;
}

.media-body:after
{
  display: block;
  content: '';
  border-bottom: solid 3px lightcoral;  
  transform: scaleX(0);  
  transition: transform 250ms ease-in-out;
}

.media-body:hover:after
{
    transform: scaleX(1);
}

.material-depth-1:hover {
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}


</style>



